<template>
  <v-container>
    <v-row dense>
      <v-col>
        <v-expansion-panels accordion hover tile v-model="showOptions">
          <v-expansion-panel>
            <v-expansion-panel-header>Options</v-expansion-panel-header>
            <v-expansion-panel-content>
              <div class="header">Show additional info:</div>
              <v-checkbox dense v-model="showItems" label="Hideout items"></v-checkbox>
              <v-checkbox
                class="indent"
                :disabled="!showItems"
                dense
                v-model="showItemsWhenDone"
                label="when done"
              ></v-checkbox>
              <v-checkbox dense v-model="showRequirements" label="Hideout requirements"></v-checkbox>
              <v-checkbox
                class="indent"
                :disabled="!showRequirements"
                dense
                v-model="showRequirementsWhenDone"
                label="when done"
              ></v-checkbox>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-dialog v-model="dialog" max-width="500" persistent v-if="dialogContent" scrollable>
          <v-card style="height:400px;" v-if="!dialogContent.selected">
            <v-card-title class="headline">{{dialogContent.item.name}}</v-card-title>
            <v-divider></v-divider>
            <v-card-text>
              <h3>Remove the following items from stock?</h3>
              <v-row v-for="(value, item) in dialogContent.requiredItems" :key="item.id">
                <ItemBox
                  v-for="i in value.raid&&value.raid.amount?value.raid.amount:0"
                  :item="item"
                  :value="value"
                  :raid="true"
                  :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].raid:false"
                  :key="item.id+'r'+i"
                  :quests="value.raid.quests"
                  :hideout="value.raid.hideout"
                  @itemToggled="itemToggled"
                />
                <ItemBox
                  v-for="i in value.nonraid&&value.nonraid.amount?value.nonraid.amount:0"
                  :item="item"
                  :value="value"
                  :raid="false"
                  :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].nonraid:false"
                  :key="item.id+'n'+i"
                  :quests="value.nonraid.quests"
                  :hideout="value.nonraid.hideout"
                  @itemToggled="itemToggled"
                />
              </v-row>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogContent.yes(true)">Yes</v-btn>
              <v-btn color="blue darken-1" text @click="dialogContent.yes(false)">No</v-btn>
              <v-btn color="red darken-1" text @click="dialogContent.cancle">Cancel</v-btn>
            </v-card-actions>
          </v-card>
          <v-card style="height: 300px;" v-else>
            <v-card-title class="headline">{{dialogContent.item.name}}</v-card-title>
            <v-divider></v-divider>
            <v-card-text>
              <h3>Undo progress?</h3>
              <!-- <div>
                <ul>
                  <li>"{{dialogContent.item.name}}"</li>
                  <li v-for="c in dialogContent.item.children" :key="c.id">"{{c.name}}"</li>
                </ul>
              </div>-->
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogContent.yes">Ok</v-btn>
              <v-btn color="red darken-1" text @click="dialogContent.cancle">Cancel</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-treeview
          v-model="doneHideout"
          class="trp-light"
          selected-color="#9a8866"
          dense
          color="#f00"
          :items="hideout"
          selection-type="independent"
          open-all
          hoverable
          transition
          selectable
        >
          <template slot="label" slot-scope="{ item, selected }">
            <v-card outlined>
              <h4>{{item.name}}</h4>
              <ul v-if="(!selected || showRequirementsWhenDone) && showRequirements" class="tiny">
                <li
                  v-for="skill in item.skills"
                  :key="item.id+skill.id"
                >{{skill.id}}: {{skill.level}}</li>
                <li
                  v-for="loyalty in item.loyalty"
                  :key="item.id+loyalty.trader"
                >{{loyalty.trader}}: {{loyalty.level}}</li>
                <li
                  v-for="currency in item.money?Object.keys(item.money):[]"
                  :key="item.id+currency"
                >{{currency}}: {{item.money[currency]}}</li>
              </ul>
              <template dense v-if="(!selected || showItemsWhenDone) && showItems">
                <div
                  v-for="(value, e) in staticData.getRequiredItemsForHideout([item.id])"
                  :key="e.id"
                >
                  <ItemBox
                    v-for="i in value.raid&&value.raid.amount?value.raid.amount:0"
                    :item="e"
                    :value="value"
                    :raid="true"
                    :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].raid:false"
                    :key="item.id+'r'+i"
                    :quests="value.raid.quests"
                    :hideout="value.raid.hideout"
                    @itemToggled="itemToggled"
                  />
                  <ItemBox
                    v-for="i in value.nonraid&&value.nonraid.amount?value.nonraid.amount:0"
                    :item="e"
                    :value="value"
                    :raid="false"
                    :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].nonraid:false"
                    :key="e.id+'n'+i"
                    :quests="value.nonraid.quests"
                    :hideout="value.nonraid.hideout"
                    @itemToggled="itemToggled"
                  />
                </div>
              </template>
            </v-card>
          </template>
        </v-treeview>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import ItemBox from "./ItemBox.vue";

export default {
  name: "Hideout",
  components: { ItemBox },
  data: () => ({
    showOptions: 0,
    hideout: [],
    doneHideout: [],
    dialog: false,
    dialogContent: null,
    showItems: false,
    showItemsWhenDone: false,
    showRequirements: false,
    showRequirementsWhenDone: false,
    stocked: {}
  }),
  watch: {
    doneHideout: function(oldVal, newVal) {
      var added = newVal.filter(e => oldVal.indexOf(e) === -1);
      var removed = oldVal.filter(e => newVal.indexOf(e) === -1);
      console.log(added, removed);
      if (!this.dialog) {
        if (added && added.length > 0)
          this.toggle(this.staticData.getHideout(added[0]), true);
        else if (removed && removed.length > 0)
          this.toggle(this.staticData.getHideout(removed[0]), false);
      }
    },
    stocked: {
      handler(newVal) {
        this.$store.commit("setStocked", newVal);
      },
      deep: true
    },
    showOptions: function(newVal) {
      this.$store.commit("setShowOptionsHideout", newVal);
    }
  },
  created: function() {
    this.dialog = true;
    var hideoutTree = this.staticData.getHideoutTree();
    for (var prop in hideoutTree) {
      this.hideout.push(hideoutTree[prop]);
    }
    this.doneHideout = this.$store.state.doneHideout;
    this.stocked = this.$store.state.stocked;
    this.showOptions = this.$store.state.showOptionsHideout;
    this.dialog = false;
  },
  methods: {
    itemToggled: function(value) {
      if (value.item) {
        if (!this.stocked[value.item]) {
          this.$set(this.stocked, value.item, {
            raid: 0,
            nonraid: 0
          });
        }
        if (value.raid) {
          this.$set(
            this.stocked[value.item],
            "raid",
            this.stocked[value.item].raid + (value.checked ? -1 : 1)
          );
        } else {
          this.$set(
            this.stocked[value.item],
            "nonraid",
            this.stocked[value.item].nonraid + (value.checked ? -1 : 1)
          );
        }
      }
    },
    toggle: function(item, selected) {
      var vm = this;
      var requiredItems = this.staticData.getRequiredItemsForHideout([item.id]);
      this.dialogContent = {
        vm: this,
        selected,
        item,
        requiredItems,
        cancle: function() {
          if (!selected) {
            console.log("remove", item);
            var index = vm.doneHideout.indexOf(item.id);
            if (index !== -1) {
              vm.doneHideout.splice(index, 1);
            }
          } else {
            console.log("readd", item);
            vm.doneHideout.push(item.id);
          }
          vm.dialog = false;
        }
      };
      if (selected) {
        this.dialogContent.yes = function() {
          vm.dialog = false;
          var index = vm.doneHideout.indexOf(item.id);
          if (index !== -1) {
            vm.doneHideout.splice(index, 1);
          }
          // if (item.children) {
          //   item.children.forEach(c => {
          //     var index = vm.doneHideout.indexOf(c.id);
          //     if (index !== -1) {
          //       vm.doneHideout.splice(index, 1);
          //     }
          //   });
          // } else {
          //   index = vm.doneHideout.indexOf(item.from);
          //   if (index !== -1) {
          //     vm.doneHideout.splice(index, 1);
          //   }
          // }
          vm.$store.commit("setDoneHideout", vm.doneHideout);
        };
      } else {
        this.dialogContent.yes = function(updateStock) {
          if (updateStock) {
            vm.$store.commit("removeItemsFromStock", requiredItems);
          }
          vm.dialog = false;
          if (vm.doneHideout.indexOf(item.id) === -1) {
            vm.doneHideout.push(item.id);
          }
          // if (item.children) {
          //   item.children.forEach(c => {
          //     if (vm.doneHideout.indexOf(c.id) === -1) {
          //       vm.doneHideout.push(c.id);
          //     }
          //   });
          // } else {
          //   if (
          //     vm.quests
          //       .find(h => h.id == item.from)
          //       .children.every(c => vm.doneHideout.indexOf(c.id) !== -1)
          //   ) {
          //     if (vm.doneHideout.indexOf(item.from) === -1) {
          //       vm.doneHideout.push(item.from);
          //     }
          //   }
          // }
          vm.$store.commit("setDoneHideout", vm.doneHideout);
        };
      }
      if (
        Object.keys(vm.$store.state.stocked).filter(key =>
          vm.$store.state.stocked[key].raid
            ? vm.$store.state.stocked[key].raid
            : 0 + vm.$store.state.stocked[key].nonraid
            ? vm.$store.state.stocked[key].nonraid
            : 0
        ).length == 0 ||
        Object.keys(requiredItems).length == 0
      ) {
        console.log("requiredItems", requiredItems);
        this.dialogContent.yes();
      } else {
        this.dialog = true;
      }
    }
  }
};
</script>
<style>
.indent {
  padding-left: 10px !important;
}
.header {
  font-weight: bold;
}
.tiny {
  font-size: 0.7em;
}

.v-application--is-ltr .v-treeview-node__checkbox {
  margin-top: 5px;
}

.v-treeview-node__root {
  align-items: flex-start;
}
.v-treeview-node__label:before {
  content: "\a0";
  display: block;
  padding: 2px 0;
  line-height: 1px;
}
.v-treeview-node__label p {
  padding: 0 10px;
}
.v-input--selection-controls {
  margin-top: 0px;
  padding-top: 0px;
}
</style>